import React from "react";
import listOfHotels from "../allHotelsList/allHotelsList";
import Cards from "./cardForIndividualHotel";
import {useParams} from "react-router-dom";


function Hotels(){   
    
    const {indexNumber} = useParams();
    

    return(

        <>

            <Cards 
            key={listOfHotels[`${indexNumber}`].id}
            hotelName={listOfHotels[`${indexNumber}`].name}
            img={listOfHotels[`${indexNumber}`].imgURL}
            hotelDetails={listOfHotels[`${indexNumber}`].hotelDetails}
            mapImg={listOfHotels[`${indexNumber}`].mapImg}
            hotelRatingsAndComments={listOfHotels[`${indexNumber}`].hotelRatingsAndComments}

            />

      
         </>


    );
    

}
export default Hotels;